<template>
    <el-container>
        <el-aside class="left-group-aside" v-show="$getUserGroupSee()">
            <leftgroup
                :grouplist="groupCounts"
                @callGroup="callbackGroup"
            ></leftgroup>
        </el-aside>
        <el-main>
            <toolbar
                :buttonList="buttonList"
                @callFunction="callFunction"
                placeholder="监测对象、设备编号、设备名称、所属区域过滤"
            ></toolbar>
            <!--列表-->
            <el-table
                :data="builds"
                :row-class-name="$tableRowClassName"
                v-loading="listLoading"
                style="width: 100%"
            >
                <!-- <el-table-column type="selection" width="50"> </el-table-column> -->
                <el-table-column type="index" width="60">
                    <template slot-scope="scope">
                        <div
                            v-text="(page - 1) * pageSize + 1 + scope.$index"
                        ></div>
                    </template>
                </el-table-column>
                <el-table-column
                    prop="siteshortname"
                    label="监测对象"
                    min-width="2"
                >
                </el-table-column>
                <el-table-column
                    prop="dfcode"
                    label="设备编号"
                    min-width="2"
                ></el-table-column>
                <el-table-column prop="dfname" label="设备名称" min-width="2">
                </el-table-column>
                <el-table-column prop="dfzone" label="所属区域" min-width="2">
                </el-table-column>
                <el-table-column prop="dfstatus" label="设备状态" width="80">
                    <template slot-scope="scope">
                        <el-tag
                            disable-transitions
                            v-show="mdfstatus[scope.row.dfstatus]"
                            :type="
                                scope.row.dfstatus == '0' ? 'success' : 'danger'
                            "
                        >
                            {{ mdfstatus[scope.row.dfstatus] }}
                        </el-tag>
                    </template>
                </el-table-column>
                <el-table-column prop="bsheild" label="警戒状态" width="80">
                    <template slot-scope="scope">
                        <el-tag
                            :type="scope.row.bsheild == '1' ? '' : 'danger'"
                            disable-transitions
                            >{{
                                scope.row.bsheild == "1" ? "布防" : "撤防"
                            }}</el-tag
                        >
                    </template>
                </el-table-column>
                <el-table-column
                    prop="checkintime"
                    label="最后上线时间"
                    min-width="3"
                >
                </el-table-column>
                <el-table-column
                    prop="checkouttime"
                    label="最后离线时间"
                    min-width="3"
                >
                </el-table-column>
                <el-table-column prop="OfflineDays" label="离线天数" width="80">
                    <template
                        slot-scope="scope"
                        v-if="scope.row.OfflineDays > 0"
                    >
                        <div>{{ scope.row.OfflineDays }}天</div>
                    </template>
                </el-table-column>
                <el-table-column
                    label="操作"
                    width="150"
                    v-if="canEdit || canDelete"
                >
                    <template slot-scope="scope">
                        <el-button
                            v-if="canEdit"
                            plain
                            type="warning"
                            size="small"
                            @click="handleEdit(scope.$index, scope.row)"
                            >编辑</el-button
                        >
                        <el-button
                            v-if="canDelete"
                            plain
                            type="danger"
                            size="small"
                            @click="handleDel(scope.$index, scope.row)"
                            >删除</el-button
                        >
                    </template>
                </el-table-column>
            </el-table>

            <!--工具条-->
            <el-col :span="24" class="toolbar">
                <el-pagination
                    layout="total, prev, pager, next, jumper"
                    @current-change="handleCurrentChange"
                    :page-size="pageSize"
                    :current-page="page"
                    :total="total"
                    style="float: right"
                >
                </el-pagination>
            </el-col>

            <!--编辑界面-->
            <el-dialog
                title="编辑"
                :visible.sync="editFormVisible"
                v-model="editFormVisible"
                :close-on-click-modal="false"
            >
                <el-form
                    :model="editForm"
                    label-width="120px"
                    :rules="editFormRules"
                    ref="editForm"
                >
                    <el-col :span="12">
                        <el-form-item label="监测对象分组" prop="GROUPID">
                            <el-select
                                v-model="editForm.GROUPID"
                                filterable
                                placeholder="请选择"
                                @change="groupChange"
                            >
                                <el-option
                                    v-for="item in groups"
                                    :key="item.GROUPID"
                                    :label="item.groupshortname"
                                    :value="item.GROUPID"
                                >
                                </el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="监测对象" prop="SITEID">
                            <el-select
                                v-model="editForm.SITEID"
                                filterable
                                placeholder="请选择"
                            >
                                <el-option
                                    v-for="item in selectSites"
                                    :key="item.SITEID"
                                    :label="item.siteshortname"
                                    :value="item.SITEID"
                                >
                                </el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="设备名称" prop="dfname">
                            <el-input
                                v-model="editForm.dfname"
                                auto-complete="off"
                                maxlength="50"
                            ></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="设备编号" prop="dfcode">
                            <el-input
                                v-model="editForm.dfcode"
                                auto-complete="off"
                                maxlength="50"
                            ></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="所属区域" prop="dfzone">
                            <el-col :span="8">
                                <el-select
                                    v-model="editForm.mblock"
                                    clearable
                                    placeholder="请选择幢号"
                                >
                                    <el-option
                                        v-for="item in 99"
                                        :key="item"
                                        :label="item + '#'"
                                        :value="item + '#'"
                                    ></el-option>
                                </el-select>
                            </el-col>
                            <el-col :span="8">
                                <el-select
                                    v-model="editForm.mfloor"
                                    clearable
                                    placeholder="请选择层"
                                >
                                    <el-option
                                        v-for="item in mfloor"
                                        clearable
                                        :key="item"
                                        :label="item"
                                        :value="item"
                                    ></el-option>
                                </el-select>
                            </el-col>
                            <el-col :span="8">
                                <el-select
                                    v-model="editForm.mzone"
                                    placeholder="请选择区"
                                >
                                    <el-option
                                        clearable
                                        v-for="item in mzone"
                                        :key="item"
                                        :label="item"
                                        :value="item"
                                    ></el-option>
                                </el-select>
                            </el-col>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="初始状态" prop="bsheild">
                            <el-switch
                                v-model="editForm.bsheild"
                                active-text="布防"
                                inactive-text="撤防"
                                active-value="1"
                                inactive-value="0"
                            >
                            </el-switch>
                        </el-form-item>
                    </el-col>
                </el-form>
                <div slot="footer" class="dialog-footer">
                    <el-button @click.native="editFormVisible = false"
                        >取消</el-button
                    >
                    <el-button
                        type="primary"
                        @click.native="editSubmit"
                        :loading="editLoading"
                        >提交</el-button
                    >
                </div>
            </el-dialog>

            <!--新增界面-->
            <el-dialog
                title="新增"
                :visible.sync="addFormVisible"
                v-model="addFormVisible"
                :close-on-click-modal="false"
            >
                <el-form
                    :model="addForm"
                    label-width="120px"
                    :rules="addFormRules"
                    ref="addForm"
                >
                    <el-col :span="12">
                        <el-form-item label="监测对象分组" prop="GROUPID">
                            <el-select
                                v-model="addForm.GROUPID"
                                filterable
                                placeholder="请选择"
                                @change="groupChange"
                            >
                                <el-option
                                    v-for="item in groups"
                                    :key="item.GROUPID"
                                    :label="item.groupshortname"
                                    :value="item.GROUPID"
                                >
                                </el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="监测对象" prop="SITEID">
                            <el-select
                                v-model="addForm.SITEID"
                                filterable
                                placeholder="请选择"
                            >
                                <el-option
                                    v-for="item in selectSites"
                                    :key="item.SITEID"
                                    :label="item.siteshortname"
                                    :value="item.SITEID"
                                >
                                </el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="设备名称" prop="dfname">
                            <el-input
                                v-model="addForm.dfname"
                                auto-complete="off"
                                maxlength="50"
                            ></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="设备编号" prop="dfcode">
                            <el-input
                                v-model="addForm.dfcode"
                                auto-complete="off"
                                maxlength="50"
                            ></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="所属区域" prop="dfzone">
                            <el-col :span="8">
                                <el-select
                                    v-model="addForm.mblock"
                                    clearable
                                    placeholder="请选择幢号"
                                >
                                    <el-option
                                        v-for="item in 99"
                                        :key="item"
                                        :label="item + '#'"
                                        :value="item + '#'"
                                    ></el-option>
                                </el-select>
                            </el-col>
                            <el-col :span="8">
                                <el-select
                                    clearable
                                    v-model="addForm.mfloor"
                                    placeholder="请选择层"
                                >
                                    <el-option
                                        clearable
                                        v-for="item in mfloor"
                                        :key="item"
                                        :label="item"
                                        :value="item"
                                    ></el-option>
                                </el-select>
                            </el-col>
                            <el-col :span="8">
                                <el-select
                                    v-model="addForm.mzone"
                                    placeholder="请选择区"
                                >
                                    <el-option
                                        clearable
                                        v-for="item in mzone"
                                        :key="item"
                                        :label="item"
                                        :value="item"
                                    ></el-option>
                                </el-select>
                            </el-col>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="初始状态" prop="bsheild">
                            <el-switch
                                v-model="addForm.bsheild"
                                active-text="布防"
                                inactive-text="撤防"
                                active-value="1"
                                inactive-value="0"
                            >
                            </el-switch>
                        </el-form-item>
                    </el-col>
                </el-form>
                <div slot="footer" class="dialog-footer">
                    <el-button @click.native="addFormVisible = false"
                        >取消</el-button
                    >
                    <el-button
                        type="primary"
                        @click.native="addSubmit"
                        :loading="addLoading"
                        >提交</el-button
                    >
                </div>
            </el-dialog>
        </el-main>
    </el-container>
</template>
<script>
import {
    getDefencepagelist,
    getDefenceGroups,
    removeDefence,
    editDefence,
    addDefence,
} from "../../api/api";
import leftgroup from "../../components/LeftGroup";
import { getButtonList } from "../../promissionRouter";
import Toolbar from "../../components/Toolbar";
import dicmixin from "../../assets/dic/dicmixin";
export default {
    components: { leftgroup, Toolbar },
    mixins: [dicmixin],
    data() {
        return {
            groupCounts: [],
            buttonList: [],
            filters: {
                name: "",
            },
            builds: [],
            total: 0,
            page: 1,
            pageSize: 10,
            listLoading: false,
            addDialogFormVisible: false,
            editFormVisible: false, //编辑界面是否显示
            editLoading: false,
            editFormRules: {
                GROUPID: [
                    {
                        required: true,
                        message: "请选择监测对象分组",
                        trigger: "blur",
                    },
                ],
                SITEID: [
                    {
                        required: true,
                        message: "请选择监测对象",
                        trigger: "blur",
                    },
                ],
                dfcode: [
                    {
                        required: true,
                        message: "请填写设备编号",
                        trigger: "blur",
                    },
                ],
                dfname: [
                    {
                        required: true,
                        message: "请填写设备名称",
                        trigger: "blur",
                    },
                ],
            },
            editForm: {},
            addFormVisible: false,
            addLoading: false,
            addFormRules: {
                GROUPID: [
                    {
                        required: true,
                        message: "请选择监测对象分组",
                        trigger: "blur",
                    },
                ],
                SITEID: [
                    {
                        required: true,
                        message: "请选择监测对象",
                        trigger: "blur",
                    },
                ],
                dfcode: [
                    {
                        required: true,
                        message: "请填写设备编号",
                        trigger: "blur",
                    },
                ],
                dfname: [
                    {
                        required: true,
                        message: "请填写设备名称",
                        trigger: "blur",
                    },
                ],
            },
            addForm: {},
            canEdit: false,
            canDelete: false,
            groups: [],
            selectSites: [],
            selectGroupId: 0,
            mfloor: [],
            mzone: [
                "A区",
                "B区",
                "C区",
                "D区",
                "E区",
                "F区",
                "G区",
                "H区",
                "I区",
                "J区",
                "K区",
                "L区",
                "M区",
                "N区",
                "O区",
                "P区",
                "Q区",
                "R区",
                "S区",
                "T区",
            ],
            mdfstatus: { 0: "闭合", 1: "断线", 2: "拆除", 3: "主机拆除" },
        };
    },
    methods: {
        callbackGroup(item) {
            this.selectGroupId = item.GROUPID;
            this.page = 1;
            this.getPageList();
        },
        callFunction(item) {
            this.filters = {
                name: item.search,
            };
            this[item.Func].apply(this, item);
        },
        getPageList(index) {
            if (index) {
                this.page = index;
            } else {
                this.page = 1;
            }
            let paras = {
                groupid: this.selectGroupId,
                keyword: this.filters.name,
                page: this.page,
                size: this.pageSize,
            };
            this.listLoading = true;
            getDefencepagelist(paras).then((res) => {
                if (res.success) {
                    this.builds = res.data.data;
                    this.total = res.data.dataCount;
                }
                this.listLoading = false;
            });
        },
        handleCurrentChange(val) {
            this.getPageList(val);
        },
        getGroupCounts() {
            getDefenceGroups().then((res) => {
                if (res.success) {
                    this.groupCounts = res.data;
                }
            });
        },
        groupChange(val) {
            this.getGroupSites(val);
            this.$set(this.addForm, "SITEID", "");
            this.$set(this.editForm, "SITEID", "");
            this.selectSites = this.groupSites;
        },
        //删除
        handleDel(index, r) {
            let row = r;
            if (!row) {
                this.$message({
                    message: "请选择要删除的一行数据！",
                    type: "error",
                });

                return;
            }
            this.$confirm("确认删除该记录吗?", "提示", {
                type: "warning",
            })
                .then(() => {
                    this.listLoading = true;
                    let para = { id: row.DEFENCEID };
                    removeDefence(para).then((res) => {
                        this.listLoading = false;
                        if (res.success) {
                            this.$message({
                                message: "删除成功",
                                type: "success",
                            });
                        } else {
                            this.$message({
                                message: res.msg,
                                type: "error",
                            });
                        }

                        this.getPageList(this.page);
                        this.getGroupCounts();
                    });
                })
                .catch(() => {});
        },
        //显示编辑界面
        async handleEdit(index, r) {
            let row = r;
            if (!row) {
                this.$message({
                    message: "请选择要编辑的一行数据！",
                    type: "error",
                });

                return;
            }
            this.editFormVisible = true;
            if (row.dfzone) {
                row.mblock = row.dfzone.split(" ")[0];
                row.mfloor = row.dfzone.split(" ")[1];
                r.mzone = row.dfzone.split(" ")[2];
            }
            this.groupChange(row.GROUPID);
            this.editForm = Object.assign({}, row);
        },
        //显示新增界面
        handleAdd() {
            this.addFormVisible = true;
            this.addForm = {};
        },
        //编辑
        editSubmit: function () {
            this.$refs.editForm.validate((valid) => {
                if (valid) {
                    //this.$confirm("确认提交吗？", "提示", {}).then(() => {
                    this.editLoading = true;
                    let para = Object.assign({}, this.editForm);
                    para.dfzone = `${para.mblock} ${para.mfloor} ${para.mzone}`;
                    editDefence(para).then((res) => {
                        if (res.success) {
                            this.$message({
                                message: "修改成功",
                                type: "success",
                            });
                            this.$refs["editForm"].resetFields();
                            this.editFormVisible = false;
                            this.getPageList(this.page);
                            this.getGroupCounts();
                        } else {
                            this.$message({
                                message: res.msg,
                                type: "error",
                            });
                        }

                        this.editLoading = false;
                    });
                    //});
                }
            });
        },
        //新增
        addSubmit: function () {
            let _this = this;
            this.$refs.addForm.validate((valid) => {
                if (valid) {
                    //this.$confirm("确认提交吗？", "提示", {}).then(() => {
                    this.addLoading = true;
                    let para = Object.assign({}, this.addForm);
                    para.dfzone = `${para.mblock} ${para.mfloor} ${para.mzone}`;
                    addDefence(para).then((res) => {
                        if (res.success) {
                            this.$message({
                                message: "新增成功",
                                type: "success",
                            });
                            this.$refs["addForm"].resetFields();
                            this.addFormVisible = false;
                            this.getPageList(this.page);
                            this.getGroupCounts();
                        } else {
                            this.$message({
                                message: res.msg,
                                type: "error",
                            });
                        }
                        this.addLoading = false;
                    });
                    //});
                }
            });
        },
    },
    created() {
        for (var i = -3; i <= 50; i++) {
            if (i < 0) this.mfloor.push("B" + i * -1);
            else this.mfloor.push(i + "F");
            if (i == 0) this.mfloor.pop();
        }
    },
    mounted() {
        this.getPageList();
        this.getGroupCounts();
        this.getGroups();
        let routers = window.sessionStorage.router
            ? JSON.parse(window.sessionStorage.router)
            : [];
        let buttons = getButtonList(this.$route.path, routers);
        buttons.forEach((element) => {
            //&& element.IsButton && !element.IsHide
            if (element.Func == "handleEdit") {
                this.canEdit = true;
            } else if (element.Func == "handleDel") {
                this.canDelete = true;
            } else {
                this.buttonList.push(element);
            }
        });
    },
};
</script>
<style scoped>
.left-group {
    padding: 0px 10px;
}
.el-select {
    width: 100%;
}
</style>


